'use client';
import { useToast } from '@/hooks/use-toast';
import { getStatusStyle } from './buttonStatus';
import {
  Dialog,
  DialogTrigger,
  DialogContent,
  DialogTitle,
  DialogDescription,
  DialogClose,
} from '@/components/ui/dialog';
import React, { useState, useEffect } from 'react';
import {
  History,
  CheckCircle,
  XCircle,
  Clock as PendingIcon,
  Search,
  MapPin,
  University,
  Flame,
  Rocket,
  Stars,
  ChevronLeft,
  ChevronRight,
  Type,
  Calendar1,
  Building,
  List,
  Calendar,
  ShieldCheck,
  Clock,
  X,
  Download,
} from 'lucide-react';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { motion, AnimatePresence } from 'framer-motion';
import EventCard from '@/components/ui/EventCard';
import { events } from '@/constants/events';
import { EventCategory } from '@/types/type';
import { useDebounce } from 'use-debounce';
import Link from 'next/link';
import { Calendar as CalendarComp } from '@/components/ui/calendar';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover';

const itemVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.5,
      ease: 'easeOut',
    },
  },
  hover: {
    scale: 1.02,
    transition: {
      duration: 0.3,
    },
  },
};
// تسميات الفلاتر
const filterLabels = [
  { label: 'All', icon: List, value: 'all' },
  { label: 'upcomping', icon: Flame, value: 'upcomping' },
  { label: 'Past', icon: History, value: 'past' },
  { label: 'Pending', icon: PendingIcon, value: 'pending' },
  { label: 'Approved', icon: CheckCircle, value: 'approved' },
  { label: 'Rejected', icon: XCircle, value: 'rejected' },
];

const AllEventsPage = ({ className }: React.HTMLAttributes<HTMLDivElement>) => {
  const { toast } = useToast();
  const [event, setEvents] = useState<EventCategory[]>([]);
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [activeFilter, setActiveFilter] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [debouncedQuery] = useDebounce(searchQuery, 300);
  const [isLoading, setIsLoading] = useState(true);
  const [isCalendarOpen, setIsCalendarOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1); // حالة Pagination الجديدة
  const [eventsPerPage] = useState(6); // عدد الأحداث في كل صفحة
  const [loading, setLoading] = useState(false);
  const [statusButton, setStatusButton] = useState('');
  const [selectedEvent, setSelectedEvent] = useState<EventCategory | null>(
    null
  );

  const generateReport = (event: EventCategory) => {
    const doc = new jsPDF();

    doc.setFontSize(16);
    doc.text('Booking Invoice', 20, 20);

    doc.setFontSize(12);
    doc.text(`Student Name: Test User`, 20, 40);
    doc.text(`Student Email: test@example.com`, 20, 50);

    doc.text(`Event Name: ${event.title}`, 20, 70);
    doc.text(`Location: ${event.location}`, 20, 80);
    doc.text(`Date: ${new Date(event.date).toLocaleDateString()}`, 20, 90);

    doc.text(`Booking ID: #${event.registeringId || '123456'}`, 20, 110);
    doc.text('Thank you for booking with us!', 20, 130);

    doc.save(`Invoice-${event.title}.pdf`);
  };

  // تصفية الأحداث
  const filteredEvents = (events || []).filter((event) => {
    const matchesSearch = event.title
      .toLowerCase()
      .includes(debouncedQuery.toLowerCase());
    const matchesFilter =
      activeFilter === 'all' ||
      activeFilter === 'upcomping' ||
      activeFilter === 'past' ||
      (activeFilter === 'pending' && event.status === 'pending') ||
      (activeFilter === 'approved' && event.status === 'approved') ||
      (activeFilter === 'rejected' && event.status === 'rejected');

    return matchesSearch && matchesFilter;
  });

  // حساب الأحداث للصفحة الحالية
  const indexOfLastEvent = currentPage * eventsPerPage;
  const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
  const currentEvents = filteredEvents.slice(
    indexOfFirstEvent,
    indexOfLastEvent
  );
  const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

  // تغيير الصفحة
  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  useEffect(() => {
    const fetchEvents = async () => {
      setLoading(true);

      const filterType =
        activeFilter === 'all'
          ? ''
          : activeFilter === 'upcoming'
          ? 'upcoming'
          : activeFilter === 'past'
          ? 'past'
          : '';

      const url = `${process.env.NEXT_PUBLIC_API_URL}/events?type=${filterType}&page=${currentPage}&perPage=${eventsPerPage}`;

      console.log('query : ', filterType);
      try {
        const res = await fetch(url, {
          method: 'GET',
          credentials: 'include',
        });
        const data = await res.json();
        setEvents(data.event);
      } catch (error: any) {
        // Handle connection errors or unexpected errors.
        toast({
          title: 'Error',
          description: error?.message || 'An unexpected error occurred.',
          className: 'bg-red-600 text-white border-0',
        });
      } finally {
        setLoading(false);
      }
    };

    // fetchEvents();
  }, [activeFilter, currentPage]);
  const handleStatusChange = async ({
    url,
    method = 'PATCH',
    body,
    successMessage = 'تم التحديث بنجاح',
    onSuccess,
  }: {
    url: string;
    method?: 'PATCH' | 'POST' | 'DELETE';
    body?: any;
    successMessage?: string;
    onSuccess?: () => void;
  }) => {
    try {
      setLoading(true);

      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include',
        body: body ? JSON.stringify(body) : null,
      });

      const data = await response.json();
      toast({
        title: 'تم!',
        description: successMessage,
      });

      if (onSuccess) onSuccess();
    } catch (err: any) {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: err.message,
      });
    } finally {
      setLoading(false);
    }
  };
  const onApprove = () => {
    if (!selectedEvent) return;

    handleStatusChange({
      url: `${process.env.NEXT_PUBLIC_API_URL}/api/admin/events/${selectedEvent.id}/approve`,
      method: 'PATCH',
      body: { status: 'approve' },
      successMessage: 'The event has been approved.',
      onSuccess: () => {
        setEvents((prev) =>
          prev.map((e) =>
            e.id === selectedEvent.id ? { ...e, status: 'approved' } : e
          )
        );
        setSelectedEvent(null);
      },
    });
  };
  const onReject = () => {
    if (!selectedEvent) return;

    handleStatusChange({
      url: `${process.env.NEXT_PUBLIC_API_URL}/api/admin/events/${selectedEvent.id}/reject`,
      method: 'PATCH',
      body: { status: 'reject' },
      successMessage: 'The event has been rejected.',
      onSuccess: () => {
        setEvents((prev) =>
          prev.map((e) =>
            e.id === selectedEvent.id ? { ...e, status: 'approved' } : e
          )
        );
        setSelectedEvent(null);
      },
    });
  };

  // إعادة تعيين الصفحة إلى 1 عند تغيير الفلتر
  useEffect(() => {
    setCurrentPage(1);
  }, [activeFilter]);

  return (
    <main className='bg-white min-h-screen relative overflow-hidden'>
      <div className='max-w-6xl mx-auto px-4 relative'>
        {/* Controls */}
        <motion.div
          className='flex flex-col lg:flex-row gap-4 items-stretch my-8'
          variants={itemVariants}
        >
          {/* Search Input */}
          <div className='relative flex-1 max-w-full'>
            <div className='absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none'>
              <Search className='h-5 w-5 text-purple-400' />
            </div>
            <Input
              placeholder='Search events...'
              className='pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent w-full'
              value={searchQuery}
              onChange={(e) => {
                setSearchQuery(e.target.value);
                setCurrentPage(1);
              }}
            />
          </div>

          {/* Filters Tabs */}
          <div className='flex-1 overflow-x-auto'>
            <Tabs
              value={activeFilter}
              onValueChange={(val) => {
                setActiveFilter(val);
                setCurrentPage(1);
              }}
              className='w-full'
            >
              <TabsList className='bg-transparent gap-1 p-1 flex-wrap justify-start w-full'>
                {filterLabels.map(({ label, icon: Icon, value }) => (
                  <TabsTrigger
                    value={value}
                    key={value}
                    className='data-[state=active]:bg-purple-600 data-[state=active]:text-white rounded-full px-3 py-1 text-xs md:text-sm md:px-4 md:py-2 transition-all whitespace-nowrap'
                  >
                    <div className='flex items-center gap-1 md:gap-2'>
                      <Icon className='h-3 w-3 md:h-4 md:w-4' />
                      {label}
                    </div>
                  </TabsTrigger>
                ))}
              </TabsList>
            </Tabs>
          </div>

          {/* Calendar Popover */}
          <Popover
            open={isCalendarOpen}
            onOpenChange={setIsCalendarOpen}
          >
            <PopoverTrigger asChild>
              <Button
                variant='outline'
                className='rounded-full gap-2'
              >
                <Calendar className='h-4 w-4' />
                <span>Select Date</span>
              </Button>
            </PopoverTrigger>
            <PopoverContent
              className='w-auto p-0'
              align='end'
            >
              <CalendarComp
                mode='single'
                selected={date}
                onSelect={(selectedDate) => {
                  setDate(selectedDate);
                  setIsCalendarOpen(false);
                }}
                initialFocus
              />
            </PopoverContent>
          </Popover>
        </motion.div>
        <motion.div
          className='grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8'
          initial='hidden'
          animate='visible'
          variants={{
            hidden: { opacity: 0 },
            visible: {
              opacity: 1,
              transition: {
                staggerChildren: 0.1,
                delayChildren: 0.2,
              },
            },
          }}
        >
          <AnimatePresence>
            {currentEvents.map((event, index) => {
              const statusInfo = getStatusStyle(event.status);
              return (
                <motion.div
                  key={event.id}
                  variants={{
                    hidden: { opacity: 0, y: 20 },
                    visible: { opacity: 1, y: 0 },
                  }}
                  whileHover={{
                    y: -5,
                    transition: { type: 'spring', stiffness: 300 },
                  }}
                  className='relative group'
                >
                  <EventCard
                    event={event}
                    className='transition-all duration-300 group-hover:shadow-xl
                                                        group-hover:shadow-purple-500/20 group-hover:border-cyan-400/30'
                  >
                    {/* محتوى البطاقة */}
                    <div className='p-6 bg-white shadow-xl rounded-2xl space-y-4 hover:shadow-purple-200 transition-all duration-300'>
                      <Link href={`events/${event.id}`}>
                        <div className='flex justify-between text-center'>
                          {/* التصنيف */}
                          <div className='flex text-center gap-2 bg-clip-text text-transparent bg-gradient-to-r from-pink-600 via-purple-400 to-indigo-400'>
                            <span className='text-3xl font-medium'>
                              {event.category}
                            </span>
                          </div>
                          <div
                            className={`flex items-center gap-2 px-3 text-sm font-medium h-8 rounded-full ${statusInfo.className}`}
                          >
                            {statusInfo.icon}
                            <span>{statusInfo.label}</span>
                          </div>
                        </div>
                        {/* العنوان والقسم والوقت */}
                        <div className='flex justify-between items-start gap-4 my-6'>
                          <div className='space-y-1'>
                            <div className='flex items-center gap-2 text-gray-800'>
                              <Type className='w-4 h-4 text-purple-600' />
                              <h3 className='text-lg font-bold text-purple-700'>
                                {event.title}
                              </h3>
                            </div>
                            <p className='text-sm text-gray-500'>
                              By {event.department}
                            </p>
                          </div>
                          <div className='flex flex-col items-end'>
                            <span className='text-sm text-gray-500'>Time</span>
                            <span className='text-xl font-bold text-purple-600'>
                              {event.time}
                            </span>
                          </div>
                        </div>

                        {/* الوصف */}
                        <p className='text-sm my-3 text-gray-600 leading-relaxed'>
                          {event.description}
                        </p>

                        {/* التاريخ والموقع */}
                        <div className='grid grid-cols-2 gap-2 text-sm text-gray-700'>
                          <div className='flex items-center gap-2'>
                            <Calendar1 className='w-4 h-4 text-purple-500' />
                            <span>{event.date}</span>
                          </div>
                          <div className='flex items-center gap-2'>
                            <Building className='w-4 h-4 text-purple-500' />
                            <span>Hall: {event.hall}</span>
                          </div>
                          {event.status === 'approved' ? (
                            <div className='flex items-center gap-2'>
                              <University className='w-4 h-4 text-purple-500' />
                              <span>Capacity: 10/{event.capacity}</span>
                            </div>
                          ) : event.status === 'pending' ? (
                            <div className='flex items-center gap-2'>
                              <University className='w-4 h-4 text-purple-500' />
                              <span>Capacity: {event.capacity}</span>
                            </div>
                          ) : (
                            <></>
                          )}
                          <div className='flex items-center gap-2'>
                            <MapPin className='w-4 h-4 text-purple-500' />
                            <span>{event.location}</span>
                          </div>
                          {event.prices ? (
                            <motion.div
                              initial={{ scale: 0, rotate: -15 }}
                              animate={{ scale: 1, rotate: -15 }}
                              className='absolute top-2 left-2 px-3 py-1 bg-red-500/90 rounded-md
                                                                          backdrop-blur-sm transform skew-x-12 shadow-lg'
                            >
                              <span className='text-white font-bold italic'>
                                PAID
                              </span>
                            </motion.div>
                          ) : (
                            <motion.div
                              initial={{ scale: 0, rotate: -15 }}
                              animate={{ scale: 1, rotate: -15 }}
                              className='absolute top-2 left-2 px-3 py-1 bg-green-500/90 rounded-md
                                                              backdrop-blur-sm transform skew-x-12 shadow-lg'
                            >
                              <span className='text-white font-bold italic'>
                                FREE
                              </span>
                            </motion.div>
                          )}
                        </div>
                      </Link>
                      <div className='flex justify-between text-center mt-6'>
                        {/* Button Approve or Rejected */}
                        <Dialog
                          onOpenChange={(isOpen) =>
                            !isOpen && setSelectedEvent(null)
                          }
                        >
                          <DialogTrigger asChild>
                            {event.status === 'pending' ? (
                              <div className='flex gap-2'>
                                <Button
                                  size='lg'
                                  asChild
                                  variant='ghost'
                                  onClick={() => setSelectedEvent(event)}
                                >
                                  <div
                                    className='flex items-center border-1 border-gray-300 gap-2'
                                    onClick={() => setStatusButton('approve')}
                                  >
                                    <X className='h-4 w-4 text-red-600' />
                                    <span>Reject</span>
                                  </div>
                                </Button>
                                <Button
                                  size='lg'
                                  asChild
                                  variant='ghost'
                                  onClick={() => setSelectedEvent(event)}
                                >
                                  <div
                                    className='flex items-center border-1 border-gray-300 gap-2'
                                    onClick={() => setStatusButton('reject')}
                                  >
                                    <ShieldCheck className='h-4 w-4 text-green-700' />
                                    <span>Approve</span>
                                  </div>
                                </Button>
                              </div>
                            ) : event.status === 'approved' ? (
                              <Button
                                size='lg'
                                asChild
                                variant='ghost'
                                onClick={() => setSelectedEvent(event)}
                              >
                                <div className='flex items-center border-1 border-gray-300 gap-2'>
                                  <X className='h-4 w-4 text-red-600' />
                                  <span>Reject</span>
                                </div>
                              </Button>
                            ) : (
                              <Button
                                size='lg'
                                asChild
                                variant='ghost'
                                onClick={() => setSelectedEvent(event)}
                              >
                                <div className='flex items-center border-1 border-gray-300 gap-2'>
                                  <ShieldCheck className='h-4 w-4 text-green-700' />
                                  <span>Approve</span>
                                </div>
                              </Button>
                            )}
                          </DialogTrigger>

                          <DialogContent className='max-w-md'>
                            <DialogTitle className='text-purple-700 text-lg'>
                              {event.status === 'pending'
                                ? 'Manage Pending Event'
                                : 'Confirm Deletion'}
                            </DialogTitle>

                            <DialogDescription className='text-gray-600 mt-1'>
                              {statusButton === 'approve' ||
                              event.status === 'approved' ? (
                                <>
                                  Are you sure you want to{' '}
                                  <strong>Rejecte</strong> the event:
                                  <span className='font-semibold text-black'>
                                    {' '}
                                    {selectedEvent?.title}
                                  </span>
                                  ?
                                  <br />
                                  <span className='text-sm text-red-500'>
                                    This action cannot be undone ⚠️
                                  </span>
                                </>
                              ) : (
                                <>
                                  Are you sure you want to{' '}
                                  <strong>Approve</strong> the event:
                                  <span className='font-semibold text-black'>
                                    {' '}
                                    {selectedEvent?.title}
                                  </span>
                                  ?
                                  <br />
                                  <span className='text-sm text-green-600'>
                                    This action cannot be undone ⚠️
                                  </span>
                                </>
                              )}
                            </DialogDescription>

                            <div className='flex justify-end gap-2 mt-4'>
                              <DialogClose asChild>
                                <Button variant='outline'>Cancel</Button>
                              </DialogClose>

                              {statusButton === 'reject' ||
                              event.status === 'rejected' ? (
                                <>
                                  <Button
                                    variant='default'
                                    onClick={() => onApprove()}
                                    disabled={loading}
                                  >
                                    {loading ? 'Approving...' : 'Approve'}
                                  </Button>
                                </>
                              ) : (
                                <>
                                  <Button
                                    variant='destructive'
                                    onClick={() => onReject()}
                                    disabled={loading}
                                  >
                                    {loading ? 'Rejecting...' : 'Reject'}
                                  </Button>
                                </>
                              )}
                            </div>
                          </DialogContent>
                        </Dialog>
                      </div>
                      <div className='flex justify-between items-center text-center mt-4'>
                        <Button
                          onClick={() => generateReport(event)}
                          className='mt-3 bg-purple-500 text-white'
                        >
                          <Download size={16} /> Download Report
                        </Button>
                        {/* الأسعار */}

                        {event.prices && (
                          <div className='flex flex-wrap gap-2'>
                            <span className='bg-purple-50 text-purple-800 font-medium px-3 py-1 rounded-full text-sm shadow-sm'>
                              {event.prices} UE
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  </EventCard>
                  <motion.div
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    className='absolute top-2 right-2 p-2 bg-purple-600/80 rounded-full
                                    backdrop-blur-sm'
                  >
                    <Rocket
                      size={18}
                      className='text-cyan-400'
                    />
                  </motion.div>
                </motion.div>
              );
            })}
          </AnimatePresence>
        </motion.div>

        {/* Pagination Controls */}
        {filteredEvents.length > eventsPerPage && (
          <div className='flex justify-center mt-12'>
            <nav className='flex items-center gap-2'>
              <button
                onClick={() => paginate(Math.max(1, currentPage - 1))}
                disabled={currentPage === 1}
                className={`p-2 rounded-full ${
                  currentPage === 1
                    ? 'opacity-50 cursor-not-allowed'
                    : 'hover:bg-purple-500/20'
                }`}
              >
                <ChevronLeft className='text-purple-400' />
              </button>

              {Array.from({ length: totalPages }).map((_, index) => (
                <button
                  key={index}
                  onClick={() => paginate(index + 1)}
                  className={`w-10 h-10 rounded-full flex items-center justify-center
                                                ${
                                                  currentPage === index + 1
                                                    ? 'bg-purple-600 text-white'
                                                    : 'text-purple-400 hover:bg-purple-500/20'
                                                } `}
                >
                  {index + 1}
                </button>
              ))}

              <button
                onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                disabled={currentPage === totalPages}
                className={`p-2 rounded-full ${
                  currentPage === totalPages
                    ? 'opacity-50 cursor-not-allowed'
                    : 'hover:bg-purple-500/20'
                } `}
              >
                <ChevronRight className='text-purple-400' />
              </button>
            </nav>
          </div>
        )}
      </div>

      {/* Decorations */}
      <motion.div
        className='fixed top-0 left-0 w-full h-full pointer-events-none z-0'
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
      >
        {[...Array(8)].map((_, i) => (
          <motion.div
            key={i}
            animate={{
              x: [0, 100, 0],
              y: [0, 50, 0],
              rotate: [0, 180, 360],
            }}
            transition={{
              duration: 12 + i * 2,
              repeat: Infinity,
              ease: 'easeInOut',
            }}
            className='absolute text-purple-400/20'
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100} %`,
            }}
          >
            <Stars size={48} />
          </motion.div>
        ))}
      </motion.div>
    </main>
  );
};
export default AllEventsPage;
